
import { grammar } from './grammar';
import { data } from './data';

const fields = Object.entries(grammar.fields)
  .reduce((acc, [k,v]) => {
      acc[k] = v;
      if(v.shorthand) {
        acc[v.shorthand] = v;
      }
      return acc;
  }, {}); 

export function convertToScryfallQuery(tree, doc) {

  function getChildNodeValue(node, childName, doc) {
    const child = node.getChild(childName);
    if(child == null)      
      throw new Error(`Expected ${childName} node not found`);
    return doc.sliceString(child.from, child.to).trim();
  }

  function getOperator(node) {
    const orNode = node.getChild("Or");
    if (orNode) {
      return "or";
    }
    const andNode = node.getChild("And");
    if(andNode) {
      return "and";
    }
    throw new Error(`Expected operator node not found`);
  }
  
  function processTerm(fieldValue, keywordValue, value) {
    const field = grammar.fields[fieldValue];
    if(field) {
      const translations = field.translations[keywordValue];
      if(translations) {
        if(typeof translations === 'function') {
          return translations(value);
        }
        return `${translations}${value}`;
      }
    }
    return `${fieldValue}${value}`;
  }

  function processNode(node) {
    switch (node.name) {
      case "Program" : {
        return processNode(node.firstChild);
      } 

      case "Expression" : {
        return processNode(node.firstChild);
      }

      case "BinaryExpression":{
        const left = processNode(node.firstChild);
        const operator = getOperator(node);
        const right = processNode(node.lastChild);
        return `(${left} ${operator} ${right})`;
      }

      case "UnaryExpression": {
        const expression = processNode(node.lastChild);
        return `-${expression}`;
      }

      case "Parentheses":{
        const innerExpression = processNode(node.getChild("Expression"));
        return `(${innerExpression})`;
      }

      case "Term":{
        const fieldValue = getChildNodeValue(node, "Field", doc);
        const keywordValue = getChildNodeValue(node, "Keyword", doc);
        const value = processNode(node.getChild("Value"));
        return processTerm(fieldValue, keywordValue, value);       
      }

      case "SingleTerm": {
        const fieldValue = getChildNodeValue(node, "SingleField", doc);
        const value = processNode(node.getChild("Value"));
        return processTerm(fieldValue, fieldValue, value);
      }

      case "Value": {
        const identifierNode = node.getChild("Identifier");
        if (identifierNode) {
          return doc.sliceString(identifierNode.from, identifierNode.to).trim();
        }
        const stringNode = node.getChild("String");
        if (stringNode) {
          return doc.sliceString(stringNode.from , stringNode.to );
        }
        break;
      }

      default:        
        throw new Error('Unknown node type');
    }
  }
  
    const root = tree.topNode;
    return processNode(root);
  }